.Home {
  height: 95vh;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 50%;
  height: auto;
  margin: auto;
  align-content: center;
  justify-content: center;
}

.content h1 {
  text-align: center;
}

.content p {
  max-width: 700px;
  text-align: justify;
}

.proyectImages {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  min-height: 50%;
  height: auto;
  margin: auto;
  background-color: #253551;
  align-content: center;
}

.card {
  color: white;
  margin: 10px;
  text-align: center;
}

.card > span {
  margin-top: 10px;
}

.box {
  width: 370px;
  height: 370px;
}

img {
  width: 100%;
  height: 100%;
}

.card:hover {
  transform: scale(1.05);
  transition: 0.4s;
  cursor: pointer;
}

@media screen and (max-width: 460px) {
  .Home {
    padding-bottom: 10px;
    overflow-x: hidden;
  }
}
